export const fetchItems = (store) => {
  const { dispatch } = store;
  dispatch('sportsmans/fetchItems');
};

export const selectItems = (store, type_id = '') => {
  const { getters } = store;
  return  !!(type_id) ? getters['sportsmans/itemsByType'][type_id] || {} : getters['sportsmans/items'];
}

export const removeItem = (store, id) => {
  const { dispatch } = store;
  dispatch('sportsmans/removeItem', id);
}

export const addItem = (store, { Name, Photo, TYPE_OF_SPORT_ID, Biography, Year }) => {
  const { dispatch } = store;
  dispatch('sportsmans/addItem', { Name, Photo, TYPE_OF_SPORT_ID, Biography, Year });
}

export const updateItem = (store, { id, Name, Photo, TYPE_OF_SPORT_ID, Biography, Year }) => {
  const { dispatch } = store;
  dispatch('sportsmans/updateItem', { id, Name, Photo, TYPE_OF_SPORT_ID, Biography, Year });
}

export const selectItemById = (store, id) => {
  const { getters } = store;
  return getters['sportsmans/itemsByKey'][id] || {};
}