<template>
  <div class="list">
    <Table
      :headers="[
        {value: 'id', text: 'ID', type: 'text'},
        {value: 'Name', text: 'ФИО', type: 'text'},
        {value: 'Photo', text: 'Фотография', type: 'img'},
        {value: 'TYPE_OF_SPORT_ID', text: 'Вид спорта', type: 'select'},
        {value: 'Biography', text: 'Биография', type: 'link',},
        {value: 'Year', text: 'Год начала карьеры', type: 'date'},
        {value: 'control', text: 'Действие'},
      ]"
      :items="items"
    >
      <template v-slot:control="{ item }">
        <div class="btn-control">
          <Btn @click="onClickEdit(item.id)" class="info">Изменить</Btn>
          <Btn @click="onClickRemove(item.id)" class="danger">Удалить</Btn>
        </div>
      </template>
    </Table>
    <router-link :to="{ name: 'SportsmansEdit' }">
      <Btn class="list__create info ">Создать</Btn>
    </router-link>
  </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';

import { selectItems, removeItem, fetchItems } from '@/store/sportsmans/selectors';
import Table from '@/components/Table/Table';
import Btn from '@/components/Btn/Btn';

export default {
  name: 'SportsmansList',
  components: {
    Table,
    Btn,
  },
  props: {
    type_id: String,
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    onMounted(() => {
      fetchItems(store);
    });
    return {
      items: computed(() => selectItems(store, props.type_id)),
      onClickRemove: id => {
        const isConfirmRemove = confirm('Вы действительно хотите удалить запись?')
        if (isConfirmRemove) {
          removeItem(store, id)
        }
      },
      onClickEdit: id => {
        router.push({ name: 'SportsmansEdit', params: { id } })
      }
    }
  }

}
</script>
